@import url('../../../assets/Colors.css');

.NotificationPopover{
    border: 1px solid #eee;
    width: 400px;
    border-radius: 4px;
    padding: 1rem 0;
    box-shadow: 0 8px 16px rgba(0,0,0,.05);
}

.popover{
    max-width: 500px;
}

.NotificationPopover h5{
    font-weight: 400;
}

@media only screen and (max-width: 450px){
    .NotificationPopover{
        width: 98%;
    }
}